@import './config';

.recommendations {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    position: absolute;
    right: 0;
    top: 0;
    height: 100vh;
    width: 30vw;
    padding: 3% 3% 3% 1%;


    .apps {
        display: flex;
        flex-direction: column;
        height: auto;
        width: 100%;
        padding: 0 0 0 30px;
        margin-bottom: 50px;

        .title {
            @extend %main_medium;
            color: #CC5737;
            margin-bottom: 50px;

            span {
                font-weight: 700;
                font-size: 1.5rem;
            }
        }

        .apps_container {
            display: flex;
            flex-direction: column;
            height: auto;
            width: 100%;

            .app_card {
                display: flex;
                align-items: center;
                justify-content: flex-start;
            
                width: 70%;
                margin-bottom: 15px;
                text-decoration: none;
                color: black;
                transition: 0.3s all ease-in;
                border-radius: 15px;
                padding: 10px;
                cursor: pointer;
                

                &:hover {
                    @extend %box_shadow;
                }

                .logo {
                    @extend %center_flex;
                    border-radius: 15px;
                    height: 55px;
                    width: 55px;
                    margin-right: 30px;
                    
                    .icon {
                        font-size: 1.3rem;
                    }
                    
                }

                .content {
                    
                    h2 {
                        @extend %minor_small;
                        font-weight: 300;
                        color: $gray_text;
                        margin-bottom: 2px;

                    }
                    h3 {
                        @extend %minor_small;
                        font-weight: 700;
                        font-size: 0.9rem;
                    }
                }
            }

        }


    }


    .books {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background: #f9f9f8;
        padding: 30px;
        width: 100%;
        border-radius: 20px;

        .title {
            @extend %main_medium;
            margin-bottom: 20px;

            span {
                font-weight: 700;
                font-size: 1.5rem;
            }
        }
        
        .books_container {
            overflow: hidden;
            display: flex;
            flex-direction: column;
            height: auto;
           
            width: 100%;

            .book_card {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                text-decoration: none;
                margin-bottom: 10px;
                padding: 10px 20px;
                border-radius: 20px;
                color: black;
                transition: 0.3s all ease-in;

                &:hover {
                    background: #ffffff;
                }

                .content {
                    margin-right: 30px;
                    h2 {
                        @extend %minor_small;
                        font-weight: 700;
                        font-size: 1rem;
                        margin-bottom: 0.5rem;

                        span {
                            font-size: 0.7rem;
                            font-weight: 400;
                        }

                    }
                    h3 {
                        @extend %minor_small;
                        font-weight: 300;
                        font-size: 0.9rem;
                        color: $gray_text;
                    }
                }



                img {
                    height: 130px;
                    width: auto;
                    
                }
            }

        }

    }

}